@use '../inc/vars' as *;
.job-advert {
  .container {
    display: flex;
    gap: 160px;
    flex-direction: column;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      gap: 60px;
    }
    @media(max-width: $sm){
    
    }
    @media(max-width: $xs){
    
    }
    
  }
  &__body {
    display: flex;
    gap: 30px;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      flex-direction: column;
    }
    @media(max-width: $xs){
    
    }
    
  }
  &__sidebar {
    width: 450px;
    flex-shrink: 0;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      width: 370px;
    }
    @media(max-width: $sm){
      margin: auto;
      max-width: 370px;
      width: 100%;
    }
    @media(max-width: $xs){
    
    }
    
  }
  &__career-card {
    background-color: $text;
    padding: 80px 60px;
    @media(max-width: $xl){
    
    }
    @media(max-width: $lg){
    
    }
    @media(max-width: $md){
      
    }
    @media(max-width: $sm){
      position: relative !important;
      top: 0 !important;
      left: 0 !important;
      transform: none !important;
    }
    @media(max-width: $xs){
      padding: 40px;
    }
    
    * {
      color: $text-light;
      
    }
    .career-card {
      &__img-container {

      }
      &__img {
        &--hover {

        }
      }
      &__title {
        @extend .h3 !optional;
      }
      &__price {
        @extend .h5 !optional;
        padding-bottom: 30px;
        position: relative;
        width: 100%;
        &::before {
          height: 1px;
          bottom: 0;
          left: 0;
          right: 0;
          margin: auto;
          position: absolute;
          content: '';
          background-color: transparentize($text-light, 0.5);
        } 
      }
      &__location {
        
        
        svg {
          * {
            fill: $text-light;
            stroke: transparent !important;
          }
        }
      }
    }
    .link-arrow {
      margin-top: 25px;
      width: 100%;
      display: flex;
      align-items: center;
      gap: 10px;
      svg {
        * {
          fill: transparent !important;
      
        }
      }  
    }
    .button--stroke {
      margin-top: 80px;
      width: 100%;
      border-color: $text-light;
      @media(max-width: $xl){
      
      }
      @media(max-width: $lg){
      
      }
      @media(max-width: $md){
        
      }
      @media(max-width: $sm){
      }
      @media(max-width: $xs){
        margin-top: 45px;
      
      }
      
      &:hover {
        color: $text;
        background: $text-light;
      }
    }
  }


  &__main {
    
  }
}